import React from 'react';
import styled from 'styled-components';
import perfectDay from '../assets/perfect-day.svg';
import temp from '../assets/temp.svg';
import humidity from '../assets/humidity.svg';
import wind from '../assets/wind.svg';
import pressure from '../assets/pressure.svg';
import sunny from '../assets/sunny.svg';
import night from '../assets/night.svg';
import day from '../assets/day.svg';
import cloudyNight from '../assets/cloudy-night.svg';
import cloudy from '../assets/cloudy.svg';
import rain from '../assets/rain.svg';
import rainNight from '../assets/rain-night.svg';
import storm from '../assets/storm.svg';


export const WeatherIcons = {
    "01d": sunny,
    "01n": night,
    "02d": day,
    "02n": cloudyNight,
    "03d": cloudy,
    "03n": cloudy,
    "04d": perfectDay,
    "04n": cloudyNight,
    "09d": rain,
    "09n": rainNight,
    "10d": rain,
    "10n": rainNight,
    "11d": storm,
    "11n": storm,
    "13d": cloudy,
    "13n": cloudy,
    "50d": cloudy,
    "50n": cloudy
}


export const WeatherInfoIcons = {
    sunrise: temp,
    sunset: temp,
    humidity: humidity,
    wind: wind,
    pressure: pressure,
}

const WeatherCondition = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
    width: 100%;
    margin: 30px auto;
    & span {
        margin: 20px auto;
        font-size: 14px;
        & span:first-of-type{
            font-size: 28px;
        }
    }
    & img {
        width: 100px;
        height: 100px;
        margin: 5px auto;
    }
`;

const Location = styled.span`
    font-size: 28px;
    font-weight: bold;
`;

const WeatherInfoLabel = styled.span`
    font-size: 14px;
    font-weight: bold;
    margin: 20px 25px 10px;
    text-align: start;
    width: 90%;
`;

const WeatherInfoContainer = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
    width: 90%;
`;

const InfoContainer = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    margin: 5px 10px;
    width: 40%;
`;

const InfoIcon = styled.img`
    width: 36px;
    height: 36px;
`;

const Infolabel = styled.span`
    display: flex;
    flex-direction: column;
    font-size: 14px;
    margin: 15px;
    & span {
        font-size: 12px;
        text-transform: capitalize;
    }
`;

const WeatherInfoComponent = (props) => {
    const { name, value } = props;
    return (
        <InfoContainer>
            <InfoIcon src={WeatherInfoIcons[name]}/>
            <Infolabel>
                {value}
                <span>{name}</span>
            </Infolabel>
        </InfoContainer>
    );
}

const WeatherComponent = (props) => {
    const { 
        name,
        sys:{country, sunrise, sunset},
        wind:{speed: windSpeed},
        main:{  pressure,
                humidity,
                temp: currTemp
            },
        weather:[{main: weatherType, icon, description:weatherTypeDesc}]
    } = props.weatherInfo;

    const isDay = icon.includes("d");

    const getTime = (timeStamp) => {
        const newDate = new Date(timeStamp * 1000);
        const hours = newDate.getHours();
        const minutes = newDate.getMinutes();
        return `${hours} : ${minutes}`;
    }


    return (
        <>
            <WeatherCondition>
                <span>
                    <span id="temp">{Math.floor(currTemp - 273)}&deg;C</span>
                    | {weatherTypeDesc}
                </span>
                <img src={WeatherIcons[icon]} alt={weatherType}/>
            </WeatherCondition>
            <Location>{name}, {country}</Location>
            <WeatherInfoLabel>Weather Info</WeatherInfoLabel>
            <WeatherInfoContainer>
                <WeatherInfoComponent name={isDay?"sunset":"sunrise"} value={getTime(isDay?sunset:sunrise)}/>
                <WeatherInfoComponent name="humidity" value={humidity}/>
                <WeatherInfoComponent name="wind" value={windSpeed}/>
                <WeatherInfoComponent name="pressure" value={pressure}/>
            </WeatherInfoContainer>
        </>
    )
}


export default WeatherComponent;